/**
 * WP admin bar */
@media screen and (min-width: 782px) {
  html[lang] {
    margin-top: 0 !important; }

  #wpadminbar {
    transition: top 0.2s;
    top: -22px;
    opacity: 0;
    > * {
      visibility: hidden; }
    &:hover {
      top: 0;
      opacity: 1;
      > * {
        visibility: visible; } } } }

#main {
  display: flex;
  flex-direction: column;
  align-items: center;
  @for $i from 1 through 99 {
    section[tabindex="#{$i}"] {
      order: #{$i}; } } }


