.column i.fa {
  font-size: 10rem; }

h1.entry-title, h1.page-title {
  display: inline-block;
  background-color: black;
  padding: 10px;
  color: $white;
  @media (max-width: $breakpoint3) {
    display: table; } }

.grid {
  article.has-post-thumbnail {
    h1.entry-title, h1.page-title {} } }
//display: block

.slider figure figcaption h1 {
  margin-bottom: 10px;
  display: inline-block;
  background-color: transparentize(white, 0.1);
  color: black; }


//frontsite
section[rel="Monitoring"] {
  input:not([type="submit"]), textarea {
    width: 100%; } }

#mc_embed_signup, .wpcf7 {
  position: relative;
  .ajax-loader {
    display: none !important; }
  .fa-circle-o-notch {
    font-size: 2em !important;
    margin-left: 20px;
    visibility: hidden; } }


div.wpcf7-mail-sent-ok, .mc4wp-success {
  border: none;
  border-left: 3px solid #398f14;
  margin: 0;
  padding: 0 10px; }


div.wpcf7-mail-sent-ng {
  border: none;
  border-left: 3px solid #ff0000;
  margin: 0;
  padding: 0 10px; }

div.wpcf7-spam-blocked {
  border: none;
  border-left: 3px solid #ffa500;
  margin: 0;
  padding: 0 10px; }

div.wpcf7-validation-errors {
  border: none;
  border-left: 3px solid #f7e700;
  margin: 0;
  padding: 0 10px; }

section[rel="News"] {
  .more-link {
    margin: 0; }
  .entry-footer {
    padding: 30px 0; } }
