.badge {

  @media (max-width: $breakpoint3) {
    padding: 0;
    margin: 0;
    position: relative !important;
    left: 0;
    margin: 0 auto; }
  .textcontainer {
    position: relative;
    padding: 0;
    margin: 0; } }

.search-no-results, .error404 {
  #main {} }

section.not-found {
  @media (max-width: $breakpoint3) {
    padding: 0;
    width: calc(100% - 10px); }
  &.badge {
    >div {
      &::before {
        top: 0;
        left: 0;
        width: 200px;
        height: 200px;
        background-image: url("img/donut.svg"); } } } }

body.nl-open {
  overflow-y: hidden;
  #masthead, #content {
    filter: blur(4px); } }

#nlpopup {
  position: fixed;
  opacity: 0;
  position: fixed;
  pointer-events: none;
  transition: opacity .3s ease;
  color: $darkgrey;
  z-index: 999999999999999;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  .badge:before {
    top: -50px;
    left: -50px;
    width: 270px;
    height: 270px; }
  .textcontainer {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    background: #fff no-repeat 30px 380px;
    &::before {} }
  @media (max-width: $breakpoint3) {
    display: none; }
  &:before {
    content: "";
    background-color: rgba(0, 0, 0, 0.5);
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    position: fixed;
    z-index: -1; }

  &.active {
    opacity: 1;
    pointer-events: all;
    .textcontainer {
      opacity: 1;
      background-color: white; } } }

#nlpopup {
  h1 {
    line-height: 36px;
    padding: 0;
    margin: 60px 0 20px;
    color: black;
    font-size: 4rem; }
  .closex {
    display: block;
    cursor: pointer;
    width: 19px;
    height: 19px;
    margin: 0;
    padding: 0;
    position: absolute;
    top: 20px;
    right: 20px;
    &:before {
      font-size: 2em; } } }
